.App {
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
  flex-direction: column;
  height: 100vh;

}

body{
  margin: 0px;
  padding: 0px;
}

.searchSection{
  flex: 50%;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: rgb(38, 92, 92);
  color: white;
}

.dealsSection {
  width: 100%;
  flex: 50%;
  display: flex;
  align-items: center;
  flex-direction: column;
}

input, button {
  width: 300px;
  height: 50px;
  border: none;
  border-radius: 8px;
  margin: 20px;
  font-size: 22px;
}

input {
  padding-left: 10px;
  
}

button {
  cursor: pointer;
}

.games {
  width: 100%;
  height: 250px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.game {
  width: 100px;
  height: 120px;
  margin: 20px;
  border-radius: 7px;
  background-color: white;
  color: black;
  padding: 5px;
 
  
}

.game img {
  width: 80%;
  margin-top: 10px;
}

#deals {
  background-color: rgb(38, 92, 92);
  color: white;
  width: 200px;
  height: 300px;
}